export default {
	data() {
		return {
			windowHeight: 0
		};
	},
	components:{
	},
	onLoad(options) {
		this.initHeight()
	},
	methods:{
		initHeight () {
			let that = this
			uni.getSystemInfo({
			    success: function (res) {
			        console.log(res.windowHeight);
					let clientHeight = res.windowHeight,
						clientWidth = res.windowWidth,
						rpxR = 750 / clientWidth;
					that.windowHeight = clientHeight * rpxR
			    }
			});
		},
	}
}
